import React, {Component} from 'react';
import {Carousel,Card } from "antd";
import './../ui.less'

export default class Carousels extends Component{


    handleChange=(a,b,c)=>{
        console.log(a,b,c)
    }
    render() {
        return (
            <div>
                <Card title='基础轮播'>
                    <Carousel afterChange={this.handleChange} autoplay>
                        <div><h3>1</h3></div>
                        <div><h3>2</h3></div>
                        <div><h3>3</h3></div>
                        <div><h3>4</h3></div>
                    </Carousel>
                </Card>
                <Card title='图片轮播' className='pic-carousel'>
                    <Carousel autoplay>
                        <div><img src="/carousel-img/carousel-1.jpg" alt=""/></div>
                        <div><img src="/carousel-img/carousel-2.jpg" alt=""/></div>
                        <div><img src="/carousel-img/carousel-3.jpg" alt=""/></div>
                        <div><img src="/carousel-img/carousel-1.jpg" alt=""/></div>
                    </Carousel>
                </Card>
            </div>
        );
    }

}
